.Footer {
    background: url("https://media.discordapp.net/attachments/1121751668304920610/1123114452854775858/MaskGroup2.webp?width=1440&height=491");
    height: 33vw;
}

.Footer-1 {
    display: flex;
    height: 20vw;
}

.Footer-a-1 {
    width: 28%;
    padding-left: 6vw;
    margin-top: 2vw;

}

.Footer-a-2 {
    width: 25%;
}

.Footer-a-2>p,
.Footer-a-3>p,
.Footer-a-4>p {
    font-size: 1.2vw;
    line-height: 1vw;
    color: #322c2cd6;

}

.Footer-a-4>h3 {
    font-size: 1.5vw;
}

.Footer-a-3 {
    width: 25%;
}


.Footer-a-4 {
    width: 20%;

}

.f-icons {
    font-size: 2.3vw !important;
    color: #8B2E5D !important;

}

.state-name {
    height: 5vw;
}

.state-name>p {
    text-align: center;
    font-size: 1.2vw;
    color: #322c2cd6;

}

.Footer-2 {
    display: flex;
}

.Footer-2>p {
    width: 33.33%;
    text-align: center;
    font-size: 1.2vw;
    color: #322c2cd6;

}

.Footer-a-1>img {
    width: 16vw;
    height: 5vw;
}

.Footer-a-1>p {
    height: 1vw;
    font-size: 1.2vw;
    margin-right: 6vw;
    /* line-height: 0.6vw; */
    color: #322c2ce0;

}

.hr {
    display: none;
}

.CopyRight>p {
    text-align: center;
    font-size: 1.2vw;
    color: #322c2ce0;
}

@media screen and (max-width:500px) {
    .Footer-1 {
        display: block;
        height: 650px;
    }

    .Footer-a-1>img {
        width: 199px;
        height: 67px;
        margin-left: 18%;
        margin-top: 14px;
    }

    .Footer {

        height: 800px;
    }

    .Footer-a-1 {
        width: 100%;
    }

    .Footer-a-1>p {
        /* height: 0px; */
        font-size: 20px;
    }

    .Footer-a-2 {
        padding-top: 52px;
    }

    .Footer-a-2,
    .Footer-a-3,
    .Footer-a-4 {
        width: 100%;

    }

    .hr {
        display: block
    }

    .Footer-a-4>h3 {
        text-align: center;
        font-size: 14px;
    }

    .Footer-a-2>p,
    .Footer-a-3>p,
    .Footer-a-4>p {
        text-align: center;
        font-size: 15px;
        line-height: 4px;
    }

    .state-name {
        height: 60px;
    }

    .state-name>p {

        font-size: 11px;
    }

    .Footer-2>p {
        font-size: 12px;
    }

    .CopyRight>p {
        font-size: 10px;
    }
}