.text-1 {
    margin-top: -28vw;
    margin-left: 10vw;
    height: 0.1vw;
    font-size: 5vw;
    text-align: left;
    letter-spacing: 0px;
    color: #2A3133;
}

.text-2 {

    margin-left: 16vw;
    height: 10px;
    font-size: 5vw;
    text-align: left;
    letter-spacing: 0px;
    color: #2A3133;
}

.text-3 {
    margin: 9vw 0 0 7vw;
    height: 2vw;
    text-align: left;
    font-size: 4vw;
    letter-spacing: 0px;
    color: #2A3133;
}

.text-4 {
    margin: 3vw 0 0 7vw;
    height: -6vw;
    text-align: left;
    font-size: 3vw;
    letter-spacing: 0px;
    color: #2A3133;
    text-transform: uppercase;

}

.Button {
    margin: 1vw 0 0 6vw;
    width: 11vw;
    height: 2.5vw;
    background: transparent linear-gradient(253deg, #993680 0%, #2F1A59 81%) 0% 0% no-repeat padding-box;
    border-radius: 1vw;
    opacity: 1;
    border: none;
    color: white;
    font-size: 1vw;

}

@media screen and (max-width:500px) {

    .hero-section>img {
        width: 100%;
        height: 250px;
    }

    .text-1 {
        margin-top: -209px;
        font-size: 34px;
        margin-left: 25px;
    }

    .text-2 {
        margin-left: 51px;
        height: 10px;
        font-size: 34px;
    }

    .text-4 {
        font-size: 15px;
        margin: 11px 0 0 25px;
    }

    .text-3 {
        font-size: 19.5px;
        margin: 46px 0 0 26px;
        height: 9px;
    }

    .Button {
        margin: 6px 0 0 26px;
        width: 81px;
        height: 22px;
        font-size: 9.3px;
        border-radius: 8px;
    }

}