.heading_style-s {
  padding: 30px 0;
  text-align: center;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 500;
  color: #868686;
  font-size: 1.5rem;

}

.card4-text>h1 {
  width: auto;
  height: 2vw;
  text-align: center;
  font: normal normal normal 4vw/0.2vw Poppins;
  letter-spacing: 0px;
  color: #0F0D0E;
  opacity: 1;
  padding-top: 3.5vw;
}

.card4-text>p {
  width: auto;
  text-align: center;
  font: normal normal normal 1.7vw/0vw Poppins;
  letter-spacing: 0px;
  color: #0F0D0E;
  opacity: 1;
}

.cards-s {
  width: 100%;
  height: auto;
  margin-left: 2vw;
}

.card-s {
  margin: 1.5vw;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  background-color: #fff;
  width: 20.8vw;
  height: 32vw;
  border-radius: 1vw;
  /* box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1); */
  float: left;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.card-s:hover {
  box-shadow: 0 30px 18px -8px rgba(0, 0, 0, 0.1);
  transform: scale(1.05, 1.05);
}

.card_img-s {
  width: 100%;
  height: 235px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.card_info-s {
  background-color: #fff;
  text-align: center;
  background-color: #fff;
}

.star {
  text-align: center;
  color: yellow;
  font-size: 1.5vw;
}

.card_category-s {
  font-family: "Raleway " sans-serif;
  text-transform: uppercase;
  font-size: 1vw;
  letter-spacing: 2px;
  font-weight: 500;
  color: #8B2E5D;
  background-color: #fff;
}

.card_title-s {
  margin-top: 5px;
  margin-bottom: 10px;
  font-family: "Raleway " sans-serif;
  background-color: #fff;
  text-transform: capitalize;
}

.button-s-1 {

  text-align: center;
  width: 11vw;
  height: 2.5vw;
  background: transparent linear-gradient(253deg, #993680 0%, #2F1A59 81%) 0% 0% no-repeat padding-box;
  border-radius: 1vw;
  opacity: 1;
  border: none;
  color: white;
  font-size: 1vw;
}

.text-5 {
  color: red;
}

/* @media (max-width:768px){
    .card-s{
      float: none;
      margin: 5%;
      width: 86%;
    }
   } */

.card-4-container {
  background: url('../Beautys/image/images\ background\ 1.png');
  background-size: 'cover';
  background-repeat: 'no-repeat';
  background-position: 'center';
  width: auto;
  height: 82vw;
}

.card_info-s>p {
  text-align: center;
  font-size: 1.3vw;
  margin-top: -1vw;
}

.card-s>img {

  margin: 3vw 0 0 4vw;
  width: 14vw;
  height: 13vw;


}

@media screen and (max-width:500px) {

  .card4-text>h1 {
    font: normal normal normal 40px/0.2vw Poppins;
    padding-top: 27px;
  }

  .card4-text>p {
    font: normal normal normal 13px/0vw Poppins;
  }

  .card-s {
    margin: 6% 12% 8%;
    /* transition: all 0.4s cubic-bezier(0.175, 0.885, 0,1); */
    background-color: #fff;
    width: 70%;
    height: 465px;
    border-radius: 16px;
  }

  .card-s>img {
    margin: 8% 0 0 12%;
    width: 76%;
    height: 51%;
  }

  .card_category-s {
    font-size: 15px;
  }

  .star {

    font-size: 22px;
    margin-top: 4px;
  }

  .card_info-s>p {

    font-size: 19px;
    margin-top: -15px;
  }
  .button-s-1 {
    width: 113px;
    height: 29px;
    font-size: 12px;
    margin-bottom: 18px;
  }
  .card-4-container {
    height: 4270px;
  }
  
}